* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    font-family: 'Roboto', sans-serif;
}

main {
    display: block;
    max-width: 1080px;
    width: 100%;
    margin: 0 auto;
    position: relative;
}

main h1 {
    color: #060606;
    font-size: 29px;
    text-align: center;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 40px;
    margin-top: 50px;
}

main h4 {
    font-size: 16px;
    margin-top: 40px;
    text-align: center;
    font-weight: 400;
}

.quiz {
    flex-direction: column;
    justify-content: center;
    min-height: 388px;
}

.quiz.hidden {
    display: none;
}

.quiz h2 {
    font-size: 24px;
    font-weight: 800;
    margin-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
}

.quiz h3 {
    font-size: 20px;
    margin-bottom: 25px;
    text-align: center;
    white-space: pre-line;
    color: #000;
    font-weight: 400;
}

.quiz .quiz-items {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
}

.quiz .quiz-items .item {
    width: 180px;
    margin: 0 15px 20px;
    min-height: 148px;
    background: #f0f3f4;
    position: relative;
    cursor: pointer;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 5px;
    animation: anim 2s;
}

.quiz .quiz-items .item img {
    width: 70px;
    position: relative;
}

.quiz .quiz-items .item .circle {
    position: absolute;
    left: 50%;
    top: 42%;
    transform: translate(-50%, -50%);
    width: 32px;
    height: 32px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 0.15s;
    border: 1px dashed #d3d3cf;
    border-radius: 100%;
    background: #fff;
}

.quiz .quiz-items .item .circle span {
    position: relative;
    display: block;
    width: 12px;
    height: 8px;
    margin: 10px auto 0;
    transition: all 0.15s;
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    background: #d3d3cf;
}

.quiz .quiz-items .item .circle span:before {
    position: absolute;
    top: -1px;
    right: -1px;
    display: block;
    width: 10px;
    height: 6px;
    content: '';
    transition: all 0.15s;
    background: #fff;
}

.quiz .quiz-items .item .text {
    padding: 15px 15px 10px;
    position: relative;
    font-size: 15px;
    transition: all 0.15s;
    color: #000;
    line-height: 1.15;
}

.quiz .quiz-items .item:hover .text {
    color: #17b2a4;
}

.quiz-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.quiz-btn.hidden {
    display: none;
}

.prev {
    font-size: 11px;
    font-weight: 700;
    line-height: 1.2;
    cursor: pointer;
    text-transform: uppercase;
    color: #9296a5;
    border: none;
    position: relative;
    margin-left: 15px;
    transition: all 0.3s;
    border-bottom: 1px dotted #9296a5;
    background: none;
}

.hidden {
    opacity: 0;
}

.prev:before {
    position: absolute;
    top: 1px;
    right: 100%;
    content: '';
    transition: all 0.3s;
    opacity: 1;
    border: 5px solid transparent;
    margin-right: 11px;
    border-right-color: #9296a5;
}

.prev:after {
    position: absolute;
    top: 1px;
    right: 100%;
    content: '';
    transition: all 0.3s;
    opacity: 1;
    border: 5px solid transparent;
    margin-right: 9px;
    border-right-color: #fff;
}

.prev:hover {
    border-bottom: 1px dotted transparent;
}

.next {
    font-size: 13px;
    font-weight: 700;
    position: relative;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    padding: 22px 41px;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 0.15s;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    border: 0;
    border-radius: 50px;
    background: #16b2a4;
    box-shadow: 0 13px 36px 0 rgba(22, 178, 164, 0.4);
    -ms-flex-align: center;
    align-items: center;
}

.next .arrow {
    position: relative;
    height: 0;
    margin-left: 10px;
    border: 4px solid transparent;
    border-left-color: #fff;
}

.next .arrow:before {
    position: absolute;
    top: -4px;
    left: -6px;
    content: '';
    border: 4px solid transparent;
    border-left-color: #16b2a4;
}

.next:hover {
    opacity: 0.9;
    box-shadow: 0 15px 40px 0 rgba(22, 178, 164, 0.4);
}

.next:active {
    transform: scale(0.98);
    opacity: 0.9;
    box-shadow: 0 5px 20px 0 rgba(22, 178, 164, 0.4);
}

.step {
    font-size: 14px;
    color: #9296a5;
    min-width: 132px;
    text-align: right;
}

.step span {
    color: #000;
}

.slide-bar {
    flex-direction: column;
    justify-content: center;
    min-height: 523px;
}

.slide-bar.hidden {
    display: none;
}

.slide-bar h2 {
    font-size: 24px;
    font-weight: 800;
    line-height: normal;
    margin-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
}

.slide-bar .slide {
    width: 100%;
    height: 30px;
    background: #17b2a4;
    border-radius: 20px;
    animation: slidein 2s;
}

.form-block {
    flex-direction: column;
    justify-content: center;
    min-height: 523px;
}

.form-block h2 {
    font-size: 20px;
    line-height: normal;
    margin-bottom: 25px;
    text-align: center;
    white-space: pre-line;
    color: #000;
    font-weight: 400;
}

.form-block #form {
    max-width: 380px;
    width: 100%;
    margin: 0 auto;
}

.form-block #form .input-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 56px;
    transition: border-color 100ms ease;
    border: 1px solid #c0c4c3;
    border-radius: 5px;
    margin-bottom: 10px;
}

.form-block #form .input-wrapper:before {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 58px;
    content: '';
    background-color: #f0f3f4;
    background-repeat: no-repeat;
    background-position: center;
}

.form-block #form .input-wrapper input {
    font-size: 16px;
    line-height: 54px;
    display: block;
    width: 100%;
    height: 100%;
    padding: 0 0 0 78px;
    border: none;
    border-radius: 5px;
}

.form-block #form .input-person:before {
    background-image: url(https://raw.githubusercontent.com/JekaIv/ImageForReactQuiz/master/user.png);
}

.form-block #form .input-email:before {
    background-image: url(https://raw.githubusercontent.com/JekaIv/ImageForReactQuiz/master/mail.png);
}

.form-block #form .input-phone:before {
    background-image: url(https://raw.githubusercontent.com/JekaIv/ImageForReactQuiz/master/phone.png);
}

.form-block #form .input-message {
    padding: 10px;
    height: 135px;
}

.form-block #form .input-message textarea {
    font-size: 16px;
    line-height: 54px;
    display: block;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 5px;
    resize: none;
}

.form-block #form .input-message:before {
    content: none;
}

.form-block #form .submit {
    font-size: 13px;
    font-weight: 700;
    display: -ms-flexbox;
    display: flex;
    padding: 22px 41px;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all 0.15s;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    border: 0;
    border-radius: 50px;
    background: #16b2a4;
    box-shadow: 0 13px 36px 0 rgba(22, 178, 164, 0.4);
    -ms-flex-align: center;
    align-items: center;
    margin: 30px auto 0;
}

.form-block #form .submit:hover {
    opacity: 0.9;
    box-shadow: 0 15px 40px 0 rgba(22, 178, 164, 0.4);
}

.form-block #form .submit:active {
    transform: scale(0.98);
    opacity: 0.9;
    box-shadow: 0 5px 20px 0 rgba(22, 178, 164, 0.4);
}

@keyframes slidein {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@keyframes anim {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.checked {
    background: #17b2a4 !important;
}

.checked .text {
    color: #fff !important;
}

.checked .circle span {
    background: #17b2a4 !important;
}

.active {
    display: flex;
}

.error-checked {
    font-size: 14px;
    padding: 16px;
    position: absolute;
    width: 500px;
    max-width: 100%;
    color: #3c3c3c;
    border-radius: 6px;
    background: #fff;
    box-shadow: 0 0 0 6px rgba(153, 153, 153, 0.3);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.error-checked.hidden,
.error-checked-bg.hidden {
    display: none;
}

.error-checked-bg {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
}

@media all and (max-width: 600px) {
    main {
        padding: 0 10px;
    }

    .quiz .quiz-items .item {
        width: 130px;
        margin: 0 10px 20px;
    }

    .quiz .quiz-items .item img {
        width: 50px;
    }

    .quiz .quiz-items .item .text {
        font-size: 12px;
        padding-top: 45px;
    }

    .quiz-btn {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
        flex-wrap: wrap;
    }

    .step {
        width: 100%;
        margin-top: 20px;
    }

    .prev {
        width: 57px;
        height: 57px;
        border: 1px solid #d3d3cf;
        border-radius: 50%;
    }

    .prev:before {
        top: 39%;
        right: 27%;
    }

    .prev:after {
        top: 39%;
        right: 27%;
    }

    .prev span:nth-of-type(1) {
        display: none;
    }
}

